import React,{useState} from 'react'
import axios from 'axios';
import { Redirect } from 'react-router';

const Register = () => {
    const[errormsg,setErrormsg]=useState(null);
    const[exit,setExit]=useState(null);
    const [data,setData] = useState({
        username:'',
        email:'',
        password:'',
        confirmpassword:''
    })
    const changeHandler = e =>{
        setData({...data,[e.target.name]:e.target.value})
    }
    
    const submitHandler = (e) =>{
        e.preventDefault();
        axios.post("https://authentication-app-7xn0.onrender.com/register",data).then(
            res =>
             {setData({
                username:'',
                email:'',
                password:'',
                confirmpassword:''
            })}).catch(error=>{
                    if(error.response.status===404){
                        setErrormsg("confirm password should be same as password")
                    }
                    if(error.response.status===400){
                        setErrormsg("user already exist please login")
                        setTimeout(()=>{setExit(true)},3000)
                    }
                    
                
            
            })
            
        }
        if(exit){
            return <Redirect to='/login' />
        }

    return (
        <div>
            <center>
            <form onSubmit={submitHandler} id="form">
                <h3>Register</h3>
                <input type="text" onChange={changeHandler} name="username" placeholder="User Name" required /><br />
                <input type="email" onChange={changeHandler} name="email" placeholder="Email" required /><br />
                <input type="password" onChange={changeHandler} name="password" placeholder="Password"  required /><br />
                <input type="password" onChange={changeHandler} name="confirmpassword" placeholder="Confirm Password" required /><br />
                <input type="submit" value="Register" /><br />
                <div>
                    {errormsg ? <div>{errormsg}</div> : null}
                </div>
            </form>
            </center>
        </div>
    )
}

export default Register
