import React,{useState,useContext} from 'react'
import axios from 'axios';
import {store} from './App';
import { Redirect } from 'react-router';


const Login = () => {
    const [token,setToken] = useContext(store);
    const[errormsg,setErrormsg]=useState(null);
    const[exit,setExit]=useState(null);
    const [data,setData] = useState({
        email:'',
        password:'',
    })
    const changeHandler = e =>{
        setData({...data,[e.target.name]:e.target.value})
    }
    const submitHandler = (e) =>{
        e.preventDefault();
        axios.post("https://authentication-app-7xn0.onrender.com/login",data).then(
            res => setToken(res.data.token)
        ).catch(error=>{
            if(error.response.status===404){
                setErrormsg("email not found please register")
                setTimeout(()=>{setExit(true)},3000)
            }
            if(error.response.status===400){
                setErrormsg("wrong password")
            }
            
        })
      
    }

    if(token){
       return <Redirect to='/myprofile' />
    }
    if(exit){
        return <Redirect to='/register' />
    }
    
    return (
        <div>
            <center>
            <form onSubmit={submitHandler}>
                <h3>Login</h3>
                <input type="email" onChange={changeHandler} name="email" placeholder="Email" /><br />
                <input type="password" onChange={changeHandler} name="password" placeholder="Password" /><br />
                <input type="submit" value="Login" /><br />
                <div>
                    {errormsg ? <div>{errormsg}</div> : null}
                </div>
            </form>
            </center>
        </div>
    )
}

export default Login
