import React,{useContext} from 'react';
import { store } from './App';
import './App.css'
import { Redirect } from 'react-router';
const Nav = () => {
    const [token] = useContext(store)
    return (
        <div>
          <Redirect to='/login' /> 
          {!token ?
            <div>
            <h4>Home</h4> 
            </div>
            :
            <h4>Dashboard</h4>
          }
        </div>    

    )
}

export default Nav
